	module.exports = {"data":{"markdownRemark":{"html":"<h2>Reading Box Plots</h2>\n<p>Box Plots might seem unintuitive at first glance, but they are a great way to convey a lot of information of a compact format. Here’s how you can read the informations contained in a\nBox Plot:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/Boxplot.jpg?raw=true\" alt=\"BoxPlotExample\"></p>\n<p>The Box Plot has several elements that we can recognize:</p>\n<ul>\n<li>The box, which is bisected in two parts</li>\n<li>Two whiskers, above and below the box</li>\n<li>Possibly some scattered points: the outliers</li>\n</ul>\n<p>Each section of the box plot represents the interval of a quartile of the population of data. What this means, is that 25% of the data lies within the two ends of the lower whisker, 25% within the interval spanned by the bottom of the box and the median line, 25% within the median and the top of the box, and 25% in the upper whisker. Any statistical outlier is represented as a point outside of the box.</p>\n<p>In the example reported above we would have (using approximate numerical values):</p>\n<ul>\n<li>a quarter of the data between 1 and 3 (lower whisker)</li>\n<li>a quarter of the data between 3 and 5 (lower part of the box)</li>\n<li>a quarter of the data between 5 and 7.5 (upper part of the box)</li>\n<li>a quarter of the data between 7.5 and 10 (upper whisker)</li>\n<li>two outlier points above the boxplot</li>\n</ul>\n<p>Notice that the line that bisects the box represents the <strong>Median</strong> (50th percentile) and <strong>not the Mean</strong>.</p>\n<h4>More Information:</h4>\n<p>For more informations:</p>\n<ul>\n<li><a href='https://en.wikipedia.org/wiki/Box_plot' target='_blank' rel='nofollow'>Wikipedia: Box Plot</a></li>\n<li><a href='https://www.khanacademy.org/math/probability/data-distributions-a1/box--whisker-plots-a1/v/interpreting-box-plots' target='_blank' rel='nofollow'>Khan Academy: reading Box Plots(video)</a></li>\n</ul>","fields":{"slug":"/mathematics/reading-box-plots/"},"frontmatter":{"title":"Reading Box Plots"}}},"pathContext":{"slug":"/mathematics/reading-box-plots/"}}

